.experiencia {
  margin-bottom: 2rem;
}
@media (min-width: 541px) {
  .experiencia .grid-sobre-mi {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 4rem;
  }
}
.experiencia .grid-sobre-mi h5 {
  text-align: center;
  font-size: 20px;
}
.experiencia .grid-sobre-mi .bg-color-fondo {
  background-color: rgba(59, 93, 209, 0.3);
  max-width: 400px;
  margin: 0 auto;
  box-shadow: 5px 5px 4px 4px #D1D1D1;
  border-radius: 1rem;
}
@media (min-width: 541px) {
  .experiencia .grid-sobre-mi .bg-color-fondo {
    box-shadow: 5px 5px 10px #D1D1D1;
  }
}
.experiencia .grid-sobre-mi .bg-color-fondo .fondo-experiencia {
  padding: 4rem;
  position: relative;
}
.experiencia .grid-sobre-mi .bg-color-fondo .fondo-experiencia::before {
  content: " ";
  position: absolute;
  height: 95%;
  border-left: 2px solid #737273;
  top: 0;
  left: 2rem;
  margin: 1rem 0;
}
.experiencia .grid-sobre-mi .bg-color-fondo .fondo-experiencia .circle-dot {
  position: absolute;
  background-color: #737273;
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  left: 1rem;
}

.footer-sobremi p {
  color: #FFFFFF;
}

.seccion.animation .sobremi-img {
  animation: 1.5s imagen ease forwards;
  overflow-x: hidden;
}
.seccion.animation .texto__sobremi {
  animation: 1.5s animarTexto ease forwards;
  overflow-x: hidden;
}

@keyframes imagen {
  0% {
    transform: translateX(-50px);
    opacity: 0;
  }
  100% {
    transform: translate(0px);
    opacity: 1;
  }
}
@keyframes animarTexto {
  0% {
    transform: translateX(50px);
    opacity: 0;
  }
  100% {
    transform: translate(0px);
    opacity: 1;
  }
}
