.bg-color{
	background-color:$azul;
	box-shadow:10px 5px 15px $azulSuave;
	border-radius:1rem;

	padding-bottom: 1rem;
	max-width: 600px;
	margin:0 auto; 

	.form-contactos{
	max-width: 600px;
	margin:0 auto;
	
	
	fieldset{
		border:none;

		legend{
			font-size: 2rem;
			text-transform: uppercase;
			padding:2rem 0;
			color:$blanco;
			text-align: center;
		}
		.nota{
			// color: $rojoFuerte;
			color:$grisFuerte;
			font-weight: bold;
		}

		.campos{
			color:$blanco;

			.campo1, .campo2, .campo3{
				width: 100%;
				padding: 0 0 1rem 0;

				input{
					padding: 1rem;
					width: 100%;

				}
				
			}

			.mensaje{
					width: 100%;
					height: 200px;
					resize: none;
				}
			.error{
				padding: 1rem 2rem;
				background-color: $rojoSuave;
				color: $rojoFuerte;
				border:1px solid $rojo;
				border-radius: 1rem;
				text-transform: capitalize;
				font-weight: bold;
			}

			input, textarea{
				border:none;
				border-radius:.5rem;

				&:hover{
					border:none;
				}
			}


			textarea{
				padding: 1rem 0 0 1rem;
			}

			@include telefono{
			display:grid;
			grid-template-columns: repeat(2, 1fr);
			column-gap: 1rem;

				.campo3, .campo4{
					grid-column: 1/3;
				}
			}

			label{
				@include telefono{
					font-size: 2rem;
				}
			}
			
		}
	}
	.flex-center{
			
			padding: 1rem;

			@include telefono{
				display: flex;
				justify-content: flex-end;

			}
			
		.boton-formulario{
			padding: 1rem 1rem;
			border:none;
			color:$grisSuave;
			background:$azulBoton;
			border-radius:1rem;
			margin-bottom: 1rem;
			width: 100%;
			font-weight: bold;
			text-transform: capitalize;
			text-shadow: 0rem 0rem .1rem $blanco;
			letter-spacing: .1rem;


			@include telefono{
				width: auto;
				padding: 1rem 3rem;
			}
			&:hover{
				cursor: pointer;
				background:$azulNav;
			}
		}
		.opacity-50{
				opacity: 0.5;
			}
	}
	
}
}
