@mixin telefono{
	@media(min-width: $telefono){
		@content;
	}
} 

@mixin tablet{
	@media(min-width: $tablet){
		@content;
	}
} 

@mixin tabletMedio{
	@media(min-width: $tabletMedio){
		@content;
	}
} 

@mixin desktopMedio{
	@media(min-width: $desktopMedio){
		@content;
	}
} 

@mixin desktop{
	@media(min-width: $desktop){
		@content;
	}
} 